import { createSlice } from '@reduxjs/toolkit';
const initialState = {
    todos: [],
    currentTodo: null,
    isLoading: false,
    filters: {},
};
const todosSlice = createSlice({
    name: 'todos',
    initialState,
    reducers: {
        setTodos: (state, action) => {
            state.todos = action.payload;
        },
        setCurrentTodo: (state, action) => {
            state.currentTodo = action.payload;
        },
        updateCurrentTodo: (state, action) => {
            if (state.currentTodo) {
                state.currentTodo = { ...state.currentTodo, ...action.payload };
            }
        },
        setFilters: (state, action) => {
            state.filters = { ...state.filters, ...action.payload };
        },
        clearFilters: (state) => {
            state.filters = {};
        },
        clearError: (state) => {
            state.error = undefined;
        },
        addTodo: (state, action) => {
            state.todos.unshift(action.payload);
        },
        updateTodo: (state, action) => {
            const index = state.todos.findIndex(todo => todo.id === action.payload.id);
            if (index !== -1) {
                state.todos[index] = action.payload;
            }
        },
        removeTodo: (state, action) => {
            state.todos = state.todos.filter(todo => todo.id !== action.payload);
        },
        toggleTodoCompleted: (state, action) => {
            const todo = state.todos.find(todo => todo.id === action.payload);
            if (todo) {
                todo.is_completed = !todo.is_completed;
                todo.sync_status = 'pending';
            }
        },
        setLoading: (state, action) => {
            state.isLoading = action.payload;
        },
        setError: (state, action) => {
            state.error = action.payload;
        },
    },
});
export const { setTodos, setCurrentTodo, updateCurrentTodo, setFilters, clearFilters, clearError, addTodo, updateTodo, removeTodo, toggleTodoCompleted, setLoading, setError, } = todosSlice.actions;
export default todosSlice.reducer;
