import { createSlice } from '@reduxjs/toolkit';
const initialState = {
    people: [],
    currentPerson: null,
    isLoading: false,
    filters: {},
};
const peopleSlice = createSlice({
    name: 'people',
    initialState,
    reducers: {
        setPeople: (state, action) => {
            state.people = action.payload;
        },
        setCurrentPerson: (state, action) => {
            state.currentPerson = action.payload;
        },
        updateCurrentPerson: (state, action) => {
            if (state.currentPerson) {
                state.currentPerson = { ...state.currentPerson, ...action.payload };
            }
        },
        setFilters: (state, action) => {
            state.filters = { ...state.filters, ...action.payload };
        },
        clearFilters: (state) => {
            state.filters = {};
        },
        clearError: (state) => {
            state.error = undefined;
        },
        addPerson: (state, action) => {
            state.people.unshift(action.payload);
        },
        updatePerson: (state, action) => {
            const index = state.people.findIndex(person => person.id === action.payload.id);
            if (index !== -1) {
                state.people[index] = action.payload;
            }
        },
        removePerson: (state, action) => {
            state.people = state.people.filter(person => person.id !== action.payload);
        },
        setLoading: (state, action) => {
            state.isLoading = action.payload;
        },
        setError: (state, action) => {
            state.error = action.payload;
        },
    },
});
export const { setPeople, setCurrentPerson, updateCurrentPerson, setFilters, clearFilters, clearError, addPerson, updatePerson, removePerson, setLoading, setError, } = peopleSlice.actions;
export default peopleSlice.reducer;
