import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
const initialState = {
    notes: [],
    currentNote: null,
    isLoading: false,
    filters: {},
};
// Async thunks
export const loadCachedNotes = createAsyncThunk('notes/loadCached', async () => {
    return await window.electronAPI.getCachedNotes();
});
export const saveNote = createAsyncThunk('notes/save', async (note) => {
    const result = await window.electronAPI.cacheNote(note);
    if (!result.success) {
        throw new Error('Failed to save note');
    }
    return note;
});
const notesSlice = createSlice({
    name: 'notes',
    initialState,
    reducers: {
        setCurrentNote: (state, action) => {
            state.currentNote = action.payload;
        },
        updateCurrentNote: (state, action) => {
            if (state.currentNote) {
                state.currentNote = { ...state.currentNote, ...action.payload };
            }
        },
        setFilters: (state, action) => {
            state.filters = { ...state.filters, ...action.payload };
        },
        clearFilters: (state) => {
            state.filters = {};
        },
        clearError: (state) => {
            state.error = undefined;
        },
        addNote: (state, action) => {
            state.notes.unshift(action.payload);
        },
        updateNote: (state, action) => {
            const index = state.notes.findIndex(note => note.id === action.payload.id);
            if (index !== -1) {
                state.notes[index] = action.payload;
            }
        },
        removeNote: (state, action) => {
            state.notes = state.notes.filter(note => note.id !== action.payload);
        },
    },
    extraReducers: (builder) => {
        builder
            // Load cached notes
            .addCase(loadCachedNotes.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
            .addCase(loadCachedNotes.fulfilled, (state, action) => {
            state.notes = action.payload;
            state.isLoading = false;
        })
            .addCase(loadCachedNotes.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.error.message;
        })
            // Save note
            .addCase(saveNote.fulfilled, (state, action) => {
            const existingIndex = state.notes.findIndex(note => note.id === action.payload.id);
            if (existingIndex !== -1) {
                state.notes[existingIndex] = action.payload;
            }
            else {
                state.notes.unshift(action.payload);
            }
            if (state.currentNote?.id === action.payload.id) {
                state.currentNote = action.payload;
            }
        })
            .addCase(saveNote.rejected, (state, action) => {
            state.error = action.error.message;
        });
    },
});
export const { setCurrentNote, updateCurrentNote, setFilters, clearFilters, clearError, addNote, updateNote, removeNote, } = notesSlice.actions;
export default notesSlice.reducer;
